<template>
    <div class="address">
        <header>
            <a href="javascript:history.back(-1)">
                <i class="el-icon-arrow-left"></i>
            </a>
            <span class="h_s_1">编辑地址</span>
            <span class="h_s_2" v-if="isShow1" @click="isShow1=!isShow1">编辑</span>
            <span class="h_s_2" v-if="!isShow1" @click="isShow1=!isShow1">完成</span>
        </header>
        <section>
            <ul>
                <li v-for="(a,index) in addresses">
                    <div class="s1">
                        <p>详细地址：{{a.address}}</p>
                        <p>手机号：{{a.phone}}</p>
                    </div>
                    <div class="s2" v-if="!isShow1" @click="delress(a,index)"><i class="el-icon-close"></i></div>
                </li>
            </ul>
        </section>
        <router-link to="/add">
            <div class="add"><span>新增地址</span><span><i class="el-icon-arrow-right"></i></span></div>
        </router-link>
    </div>
</template>

<script>
    export default {
        name: "Address",
        data(){
            return{
                isShow1:true,
                user_id:"",
                addresses:[],
            }
        },
        created(){
            this.user_id = localStorage.getItem("user_id");
            this.axios.get("/v1/users/"+this.user_id+"/addresses").then(res=>{
                this.addresses = res;
                // console.log(res);
            }).catch(err=>{})
        },
        methods:{
            delress(a,index){
                this.axios.delete("/v1/users/"+this.user_id+"/addresses/"+a.id).then(res=>{
                    // console.log(res);
                    this.addresses.splice(index,1);
                }).catch(err=>{})
            }
        }
    }
</script>

<style scoped>
    .address{
        width: 100%;
        padding-top: 1.95rem;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    .h_s_1{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    .h_s_2{
        font-size: .7rem;
        color: #fff;
        position: absolute;
        top: 50%;
        right: 0.4rem;
        transform: translateY(-50%);
    }
    .add{
        margin-top: .4rem;
        background: #fff;
        padding: .2rem .4rem;
        border-top: 1px solid #d9d9d9;
        display: flex;
        justify-content: space-between;
    }
    .add>span:nth-child(1){
        display: block;
        font-size: .7rem;
        color: #333;
        line-height: 1.4rem;
    }
    .add>span:nth-child(2){
        display: block;
        font-size: .7rem;
        color: #333;
        line-height: 1.4rem;
    }
    section{
        width: 100%;
        margin-top: .4rem;
        border-top: 1px solid #d9d9d9;
        border-bottom: 1px solid #d9d9d9;
    }
    section ul{
        background-color: #fff;
    }
    section li{
        border-bottom: 1px solid #d9d9d9;
        padding: .4rem;
        display: flex;
        justify-content: space-between;
    }
    section li:nth-child(1){
        background: #fff8c3;
    }
    .s1 p{
        line-height: .9rem;
        font-size: .6rem;
        color: #333;
    }
    .s2{
        display: flex;
        align-items: center;
        font-size: .7rem;
        color: #999;
    }
</style>